.sub { position: absolute; top: 100px; left: 0; background-color: #313131; width: 100%; z-index: -1; padding-left: 140px; display: flex; height: 0; opacity: 0; transition: all .3s ease; visibility: hidden; }

.sub__triangle { width: 0px; height: 0px; border-style: solid; border-width: 0 7.5px 15px 7.5px; border-color: transparent transparent #313131 transparent; position: absolute; top: -15px; }

.sub .sub__block { border-left: 1px solid #454545; padding-top: 30px; padding-left: 25px; width: 200px; }

.sub h3 { color: rgba(197, 152, 80, 0.8); text-transform: uppercase; font-size: 13px; letter-spacing: 1px; margin-bottom: 20px; }

.sub ul { flex-direction: column; margin-bottom: 40px; }

