.open-menu div { border-bottom: 1px solid #646464; height: 1px; width: 18px; transition: all 0.3s; }

.open-menu div:before, .open-menu div:after { content: ''; border-bottom: 1px solid #646464; height: 1px; width: 25px; display: block; position: relative; transition: all 0.3s; }

.open-menu div:before { top: -7px; }

.open-menu div:after { top: 6px; }

.open-menu.is-open div { border: none; }

.open-menu.is-open div:before, .open-menu.is-open div:after { border-color: #ffffff; }

.open-menu.is-open div:before { transform: rotate(45deg) translateY(10px); }

.open-menu.is-open div:after { transform: rotate(-45deg) translateY(-10px); }

.mobile-menu { width: 600px; height: 100vh; position: absolute; background: #ffffff; top: 100px; z-index: 10; padding: 30px; transform: translateX(-100%); transition: transform 1s cubic-bezier(0.72, 0, 0.13, 0.99); overflow: scroll; box-sizing: border-box; }

.mobile-menu.is-open { transform: translateX(0%); }

@media only screen and (max-width: 320px) { .mobile-menu { width: 100%; top: 90px; padding: 20px; } }

.mobile-menu .header__info { margin-left: 0; }

@media only screen and (max-width: 320px) { .mobile-menu .header__info { flex-direction: column; align-items: flex-start; }
