.mobile-menu ul li { margin-bottom: 40px; }

@media only screen and (max-width: 320px) { .mobile-menu ul li { margin-bottom: 33px; } }

.mobile-menu ul li a { text-transform: uppercase; color: rgba(14, 14, 14, 0.7); letter-spacing: 1px; font-weight: 500; text-decoration: none; display: inline-block; position: relative; }

.mobile-menu ul li a.is-sub:before { content: ""; font-size: 8px; position: absolute; top: 5px; right: -25px; transform: rotate(-90deg); transition: all 0.3s; }

.mobile-menu ul li a.is-open:before { transform: rotate(0deg); }

.mobile-menu ul li a div { position: absolute; font-size: 11px; letter-spacing: 0.6px; right: -68px; top: 2px; z-index: 10; display: flex; justify-content: center; align-items: center; color: #ffffff; }

.mobile-menu ul li a div i { font-size: 19px; color: #af0808; position: absolute; z-index: -1; }

