.mobile-menu ul li .mobile-sub ul li { margin-bottom: 20px; }

.mobile-menu ul li .mobile-sub ul li a { text-transform: none; color: rgba(14, 14, 14, 0.56); }

@media only screen and (max-width: 320px) { .mobile-menu .footer__contact { flex-direction: column; }
  .mobile-menu .footer__contact__block:first-child { justify-content: flex-start; margin-right: 0; margin-bottom: 20px; margin-top: 10px; } }

@media only screen and (max-width: 320px) { .mobile-menu .footer__social { margin-top: 60px; } }

.mobile-menu .footer__view { height: 20px; display: block; }

@media only screen and (max-width: 320px) { .mobile-menu .footer__view { height: 30px; } }

.mobile-menu .footer__view p { text-align: left; }

.mobile-menu .footer__copy { height: 100px; margin-bottom: 20px; }

.footer { padding-top: 30px; padding-bottom: 30px; background: #ffffff; }

.footer__row { display: flex; justify-content: space-between; }

