body { font-family: 'Lato'; background: #F0F0F0; font-size: 15px; overflow-x: hidden; -webkit-text-size-adjust: 100%; -ms-text-size-adjust: 100%; -webkit-font-smoothing: antialiased !important; text-rendering: optimizelegibility; -moz-osx-font-smoothing: grayscale; box-sizing: border-box; }

body.menu-is-open { position: fixed; top: 0; width: 100%; left: 0; z-index: 10; }

body.menu-is-open:before { content: ''; width: 100%; height: 100%; background: rgba(56, 56, 56, 0.5); position: absolute; top: 0; left: 0; z-index: 10; }

body.menu-is-open .header { background: #383838; }

body.menu-is-open .header__phone a { color: #ffffff; border-color: #ffffff; }

body.menu-is-open .header__phone p { color: rgba(255, 255, 255, 0.5); }

body.menu-is-open .header__toolbar a i { color: #ffffff; }

@media only screen and (max-width: 320px) { .hidden-xs { display: none !important; } }

@media only screen and (min-width: 768px) and (max-width: 1023px) { .hidden-sm { display: none !important; } }

@media only screen and (min-width: 1025px) { .hidden-md { display: none !important; } }

.container { width: 100% !important; max-width: 1200px; height: 100%; box-sizing: border-box; }

@media only screen and (max-width: 768px) { .container { padding: 0 30px; } }

@media only screen and (max-width: 320px) { .container { padding: 0 20px; } }

.row { height: 100%; }

h2 { font-size: 24px; font-weight: 300; line-height: 1.08; letter-spacing: 7px; text-align: center; color: #0e0e0e; text-transform: uppercase; margin-bottom: 25px; position: relative; display: flex; justify-content: center; align-items: center; }

.btn { font-size: 15px; letter-spacing: 1px; text-transform: uppercase; color: rgba(255, 255, 255, 0.9); text-decoration: none; border: solid 1px #c59850; border-radius: 3px; transform: perspective(1px) translateZ(0); transition-property: color; transition-duration: 0.3s; position: relative; display: flex; justify-content: center; align-items: center; }

.btn-primary { background: #c59850; box-shadow: 0 12px 23px 0 rgba(0, 0, 0, 0.2); height: 56px; width: 240px; color: #ffffff; }

.btn-primary:hover:before { transform: scale(1); background: #D9A866; }

.btn-secondary { color: #c59850; height: 48px; width: 230px; }

.btn-secondary:hover { color: #ffffff; }

.btn-secondary:hover:before { transform: scale(1); background: #c59850; }

.btn:before { content: ''; position: absolute; z-index: -1; top: 0; left: 0; right: 0; bottom: 0; transform: scaleY(0); transform-origin: 50% 100%; transition-property: transform; transition: all 0.3s; }

.swiper-slide { background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.swiper-container .swiper-pagination-bullets { max-width: 1200px; text-align: left; display: flex; left: 50%; transform: translateX(-50%); bottom: 50px; }

@media only screen and (max-width: 768px) { .swiper-container .swiper-pagination-bullets { padding: 0 30px; } }

.swiper-pagination-bullet { width: 16px; height: 16px; background: #ffffff; opacity: 1; transition: all 0.3s; left: 0; text-align: left; }

.swiper-pagination-bullet-active { background: #c59850; }

.swiper-button-prev, .swiper-button-next { background: none; color: #c59850; font-size: 38px; opacity: 0.9; }

@media only screen and (max-width: 768px) { .swiper-button-prev, .swiper-button-next { display: none; } }

.swiper-button-disabled { opacity: 0.3; }

.swiper-button-prev { left: 0; }

.swiper-button-next { right: -5px; }
