html {}
body {
	font-family: 'Lato';
	background: $grey;
	font-size: 15px;
	overflow-x: hidden;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
	-webkit-font-smoothing: antialiased !important;
	text-rendering: optimizelegibility;
	-moz-osx-font-smoothing: grayscale;
	box-sizing: border-box;
	&.menu-is-open {
		position: fixed;
		top: 0;
		width: 100%;
		left: 0;
		z-index: 10;
		&:before {
			content: '';
			width: 100%;
			height: 100%;
			background: rgba(#383838, 0.5);
			position: absolute;
			top: 0;
			left: 0;
			z-index: 10; }
		.header {
			background: #383838;
			&__phone {
				a {
					color: $white;
					border-color: $white; }
				p {
					color: rgba($white, 0.5); } }
			&__toolbar {
				a i {
					color: $white; } } } } }


@include r($mobile) {
	.hidden-xs {
		display: none !important; } }
@media only screen and (min-width: 768px) and (max-width: 1023px) {
	.hidden-sm {
		display: none !important; } }
@include rmin(1025) {
	.hidden-md {
		display: none !important; } }

.container {
	width: 100% !important;
	max-width: 1200px;
	height: 100%;
	box-sizing: border-box;
	@include r($tablet) {
		padding: 0 30px; }
	@include r($mobile) {
		padding: 0 20px; } }
.row {
	height: 100%; }


h2 {
	font-size: 24px;
	font-weight: 300;
	line-height: 1.08;
	letter-spacing: 7px;
	text-align: center;
	color: $black-three;
	text-transform: uppercase;
	margin-bottom: 25px;
	position: relative;
	@include center; }

//Button
.btn {
	font-size: 15px;
	letter-spacing: 1px;
	text-transform: uppercase;
	color: rgba($white, 0.9);
	text-decoration: none;
	border: solid 1px $primary;
	border-radius: 3px;
	transform: perspective(1px) translateZ(0);
	transition-property: color;
	transition-duration: 0.3s;
	position: relative;
	@include center;
	&-primary {
		background: $primary;
		box-shadow: 0 12px 23px 0 rgba(0, 0, 0, 0.2);
		height: 56px;
		width: 240px;
		color: #ffffff;
		&:hover {
			&:before {
				transform: scale(1);
				background: #D9A866; } } }
	&-secondary {
		color: $primary;
		height: 48px;
		width: 230px;
		&:hover {
			color: $white;
			&:before {
				transform: scale(1);
				background: $primary; } } }
	&:before {
		content: '';
		position: absolute;
		z-index: -1;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		transform: scaleY(0);
		transform-origin: 50% 100%;
		transition-property: transform;
		@include tr; } }
//Slider
.swiper {
	&-slide {
		@include cover; } }
.swiper-container .swiper-pagination-bullets {
		max-width: 1200px;
		text-align: left;
		display: flex;
		left: 50%;
		transform: translateX(-50%);
		bottom: 50px;
		@include r($tablet) {
			padding: 0 30px; } }
.swiper-pagination-bullet {
	width: 16px;
	height: 16px;
	background: $white;
	opacity: 1;
	@include tr;
	left: 0;
	text-align: left;
	&-active {
		background: $primary; } }


//Slider Button
.swiper {
	&-button {
		&-prev, &-next {
			background: none;
			color: $primary;
			font-size: 38px;
			opacity: 0.9;
			@include r($tablet) {
				display: none; } }
		&-disabled {
			opacity: 0.3; }
		&-prev {
			left: 0; }
		&-next {
			right: -5px; } } }
