.card {
  display: flex;
  flex-wrap: wrap;
  margin-top: 40px;
  &__block {
    width: calc(50% - 10px);
    max-height: 400px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
    text-decoration: none;
    &:nth-of-type(odd) {
      margin-right: 20px; }
    @include r($mobile) {
      width: 100%;
      margin-right: 0 !important; }
    h2 {
      div {
        @include center;
        margin-left: 25px;
        i {
          font-size: 21px;
          color: $red;
          display: inline-block;
          transform: rotate(180deg);
          position: absolute; }
        span {
          font-size: 12px;
          letter-spacing: 1px;
          position: relative;
          z-index: 10;
          color: #ffffff;
          right: 7px;
          top: 1px; } } } }
  &__image {
    height: 400px;
    position: relative;
    @include cover;
    @include r($mobile) {
      height: 200px; }
    span {
      background: $primary;
      text-decoration: none;
      color: $white;
      font-size: 13px;
      width: 230px;
      height: 30px;
      @include center;
      border-radius: 3px;
      position: absolute;
      bottom: 50px;
      left: 50%;
      transform: translateX(-50%);
      text-transform: uppercase;
      @include tr;
      @include r($mobile) {
        bottom: 15px; }
      i {
        margin-left: 30px;
        font-size: 9px; } }
    .overlay {
      background: rgba($black, 0.8);
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      opacity: 0;
      @include center;
      @include tr;
      p {
        font-size: 14px;
        line-height: 1.43;
        letter-spacing: 5.3px;
        text-align: center;
        color: #c49850;
        border-radius: 3px;
        border: solid 1px $primary;
        display: inline-block;
        text-transform: uppercase;
        padding: 20px 30px; } }
    &:hover {
      .overlay {
        opacity: 1; }
      span {
        opacity: 0; } } } }
