.info {
  background: $white;
  padding: 35px 0;
  margin-top: 40px;
  @include r($tablet) {
    margin-top: 20px;
    background: #f9f9f9; }
  &__block {
    display: flex;
    text-decoration: none;
    @include r($tablet) {
      flex-direction: column; }
    @include r($mobile) {
      margin-bottom: 25px;
      justify-content: flex-start; }
    &__icon {
      margin-right: 15px;
      @include r($tablet) {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 12px;
        height: 50px; }
      i {
        color: $primary; } }
    &__content {
      h3 {
        color: $primary;
        font-size: 14px;
        font-weight: bold;
        letter-spacing: 1px;
        text-transform: uppercase;
        margin-bottom: 5px;
        text-align: left;
        @include r($tablet) {
          font-size: 13px; } }
      p {
        font-size: 13px;
        letter-spacing: 0.3px;
        color: rgba($black-three, 0.7);
        width: 90%;
        text-align: left; } }
    &:nth-of-type(1) i {
      font-size: 35px; }
    &:nth-of-type(2) i {
      font-size: 41px; }
    &:nth-of-type(3) i {
      font-size: 52px; }
    &:nth-of-type(4) i {
      font-size: 45px; } } }
