.header {
  height: 100px;
  background: $white;
  position: relative;
  z-index: 11;
  @include tr;
  @include r($tablet) {
    overflow: hidden;
    height: 66px; }
  @include r($mobile) {
    height: 56px; }
  .logo {
    width: 125px;
    @include r($tablet) {
      width: 85px; }
    @include r($mobile) {
      width: 75px; }
    img {
      width: 100%; } }
  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 50px;
    margin-bottom: 20px;
    a {
      display: flex;
      align-items: center;
      text-decoration: none;
      i {
        color: $primary;
        margin-right: 12px;
        position: relative;
        top: -3px; }
      p {
        color: rgba($black-two, 0.6);
        text-transform: uppercase;
        font-size: 11px;
        letter-spacing: 1px;
        padding-bottom: 2px;
        border-bottom: 3px solid transparent;
        @include tr; }
      &:hover {
        p {
          border-bottom: 3px solid $primary; } } }
    .music {
      cursor: pointer; } }
  nav {
    margin-left: 50px;
    ul {
      display: flex;
      justify-content: space-between;
      a {
        text-decoration: none;
        color: rgba($black-three, 0.7);
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 500;
        display: flex;
        align-items: center;
        letter-spacing: 1px;
        div {
          position: relative;
          font-size: 11px;
          letter-spacing: 0.6px;
          margin-left: 20px;
          z-index: 10;
          @include center;
          color: #ffffff;
          i {
            font-size: 19px;
            color: $red;
            position: absolute;
            z-index: -1; }
          span {
            padding-left: 10px;
            padding-bottom: 1px; } } }
      li {
        &:hover {
          .sub {
            height: 450px;
            opacity: 1;
            z-index: 10;
            visibility: visible; } } } } }
  &__phone {
    display: flex;
    justify-content: space-between;
    align-items: center;
    a {
      font-size: 25px;
      font-weight: 300;
      letter-spacing: 0.6px;
      text-decoration: none;
      color: $black-two;
      border-bottom: 1px dashed $black-two;
      position: relative;
      top: -3.5px; }
    p {
      text-transform: uppercase;
      font-size: 10px;
      letter-spacing: 1px;
      color: rgba($black-three, 0.5);
      width: 32%;
      line-height: 1.5; } }
  &__toolbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    a {
      i {
        font-size: 20px;
        color: #646464; } }
    .cart {
      position: relative;
      i {
        font-size: 25px; }
      &__count {
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background: $primary;
        color: $white;
        font-size: 10px;
        font-weight: 500;
        @include center;
        position: absolute;
        top: -5px;
        right: -5px; } } }
  .search {
    position: absolute;
    height: 100px;
    width: 530px;
    background-color: #f3f3f3;
    top: 0;
    right: 0;
    transform: translateX(100%);
    transition: transform 1s cubic-bezier(0.72, 0, 0.13, 0.99);
    div {
      display: flex;
      align-items: center;
      height: 100%; }
    .icon-search {
      color: #b4b4b4;
      font-size: 20px;
      position: relative;
      left: 25px; }
    input {
      background: none;
      font-family: 'Lato';
      font-size: 21px;
      font-weight: 300;
      line-height: 1.24;
      letter-spacing: 7px;
      text-align: left;
      color: rgba($black-three, 0.3);
      text-transform: uppercase;
      width: 80%;
      padding-left: 65px;
      &::placeholder {
        color: rgba($black-three, 0.3); } }
    .close {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 50px;
      font-size: 18px;
      color: #b4b4b4;
      cursor: pointer; } }
  &.fixed {
    position: fixed;
    z-index: 100;
    width: 100%;
    top: 35px;
    height: 60px;
    .logo {
      width: 70px; }
    .header__info {
      display: none; }
    .sub {
      top: 60px; } }
  &.banner-closed {
    top: 0; } }

.top__banner {
  height: 35px;
  background-image: url('../img/top-banner.jpg');
  @include cover;
  color: $white;
  z-index: 11;
  position: relative;
  .container {
    position: relative; }
  p {
    font-weight: 900;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 1px;
    @include r($mobile) {
      font-size: 9px; } }
  a {
    display: flex;
    align-items: center;
    position: relative;
    text-decoration: none;
    color: $white;
    text-transform: uppercase;
    font-size: 10px;
    letter-spacing: 1px;
    i {
      font-size: 21px;
      color: $primary;
      position: absolute;
      z-index: 1; }
    span {
      position: relative;
      z-index: 2;
      padding-left: 10px; } }
  .close {
    width: 35px;
    height: 35px;
    cursor: pointer;
    @include center;
    position: absolute;
    right: 0;
    top: 0;
    background: $primary;
    i {
      font-size: 14px; } }
  &.fixed {
    position: fixed;
    z-index: 100;
    width: 100%;
    height: 35px;
    top: 0; } }


.sub {
  position: absolute;
  top: 100px;
  left: 0;
  background-color: #313131;
  width: 100%;
  z-index: -1;
  padding-left: 140px;
  display: flex;
  height: 0;
  opacity: 0;
  transition: all .3s ease;
  visibility: hidden;
  &__triangle {
    @include arr(15, 15, #313131, t);
    position: absolute;
    top: -15px; }
  .sub__block {
    border-left: 1px solid #454545;
    padding-top: 30px;
    padding-left: 25px;
    width: 200px; }
  h3 {
    color: rgba($primary, 0.8);
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 1px;
    margin-bottom: 20px; }
  ul {
    flex-direction: column;
    margin-bottom: 40px;
    li {
      margin-bottom: 15px;
      a {
        font-size: 13px;
        text-transform: none;
        color: rgba($white, 0.7);
        letter-spacing: 0.3px;
        &:hover {
          text-decoration: underline; } }
      &:first-child {
        a {
          color: $white; } } } } }

.open-menu {
  div {
    border-bottom: 1px solid #646464;
    height: 1px;
    width: 18px;
    @include tr;
    &:before, &:after {
      content: '';
      border-bottom: 1px solid #646464;
      height: 1px;
      width: 25px;
      display: block;
      position: relative;
      @include tr; }
    &:before {
      top: -7px; }
    &:after {
      top: 6px; } }
  &.is-open {
    div {
      border: none;
      &:before, &:after {
        border-color: $white; }
      &:before {
        transform: rotate(45deg) translateY(10px); }
      &:after {
        transform: rotate(-45deg) translateY(-10px); } } } }


.mobile-menu {
  width: 600px;
  height: 100vh;
  position: absolute;
  background: #ffffff;
  top: 100px;
  z-index: 10;
  padding: 30px;
  transform: translateX(-100%);
  transition: transform 1s cubic-bezier(0.72, 0, 0.13, 0.99);
  overflow: scroll;
  box-sizing: border-box;
  &.is-open {
    transform: translateX(0%); }
  @include r($mobile) {
    width: 100%;
    top: 90px;
    padding: 20px; }
  .header__info {
    margin-left: 0;
    @include r($mobile) {
      flex-direction: column;
      align-items: flex-start;
      a {
        margin-bottom: 20px; } } }
  ul {
    li {
      margin-bottom: 40px;
      @include r($mobile) {
        margin-bottom: 33px; }
      a {
        text-transform: uppercase;
        color: rgba($black-three, 0.7);
        letter-spacing: 1px;
        font-weight: 500;
        text-decoration: none;
        display: inline-block;
        position: relative;
        &.is-sub {
          &:before {
            content: $icon-arrow-down-menu;
            @extend %icon;
            font-size: 8px;
            position: absolute;
            top: 5px;
            right: -25px;
            transform: rotate(-90deg);
            @include tr; } }
        &.is-open {
          &:before {
            transform: rotate(0deg); } }
        div {
          position: absolute;
          font-size: 11px;
          letter-spacing: 0.6px;
          right: -68px;
          top: 2px;
          z-index: 10;
          @include center;
          color: #ffffff;
          i {
            font-size: 19px;
            color: $red;
            position: absolute;
            z-index: -1; }
          span {
            padding-left: 10px;
            padding-bottom: 1px; } } }
      .mobile-sub {
        display: none;
        ul {
          padding-left: 10px;
          margin-top: 25px;
          li {
            margin-bottom: 20px;
            a {
              text-transform: none;
              color: rgba($black-three, 0.56); } } } } } }
  .footer {
    @include r($mobile) {
      &__contact {
        flex-direction: column;
        &__block {
          &:first-child {
            justify-content: flex-start;
            margin-right: 0;
            margin-bottom: 20px;
            margin-top: 10px; } } } }
    &__social {
      @include r($mobile) {
        margin-top: 60px; } }
    &__view {
      height: 20px;
      display: block;
      @include r($mobile) {
        height: 30px; }
      p {
        text-align: left; } }
    &__copy {
      height: 100px;
      margin-bottom: 20px; } } }
