.intro {
  color: $white;
  article {
    padding-top: 55px;
    @include r($mobile) {
      padding-top: 15px; }
    h1 {
      font-size: 42px;
      font-weight: 600;
      line-height: 1.43;
      letter-spacing: 5px;
      text-transform: uppercase;
      width: 32%;
      margin-bottom: 24px;
      @include r($tablet) {
        width: 55%;
        font-size: 36px;
        margin-bottom: 18px; }
      @include r($mobile) {
        width: 100%;
        font-size: 28px;
        margin-bottom: 12px; } }
    p {
      font-size: 15px;
      line-height: 1.33;
      letter-spacing: 0.3px;
      width: 32%;
      margin-bottom: 45px;
      @include r($tablet) {
        width: 65%;
        margin-bottom: 25px; }
      @include r($mobile) {
        width: 100%;
        font-size: 14px;
        margin-bottom: 20px; } } }
  &-slider {
    height: 560px;
    @include r($tablet) {
      height: 490px; }
    @include r($mobile) {
      height: 400px; } }
  .swiper-pagination {
    @include r($mobile) {
      left: 50% !important;
      transform: translateX(-50%) !important;
      width: initial !important; } }
  .swiper {
    &-button {
      &-prev, &-next {
        opacity: 1; }
      &-disabled {
        opacity: 0.5; }
      &-prev {
        left: 20px; }
      &-next {
        right: 20px; } } } }
